@import url("https://use.typekit.net/thc3faf.css");

$white: #ffffff;
$white-75: rgba($white,0.75);
$blue: #013C4B;
$primary: $blue;
$tan: #D7D1BF;
$brown: #231F20;
$light-brown: #7C6E65;
$dark-gray: #231f20;
$light-gray: #707070;

$font: "new-hero", sans-serif;
$font2: "nimbus-sans", sans-serif;


$card-border-radius: 0;

$btn-border-radius:           0;
$btn-border-radius-sm:        0;
$btn-border-radius-lg:        0;

$input-btn-padding-y:         .75rem;
$input-btn-padding-x:         2rem;
$input-btn-font-family:       $font;
$input-btn-font-size:         1.25rem;
$btn-font-weight:             700;


$position-values: (
0: 0,
5: 5%,
10: 10%,
15: 15%,
20: 20%,
25: 25%,
30: 30%,
35: 35%,
40: 40%,
45: 45%,
50: 50%,
55: 55%,
60: 60%,
65: 65%,
70: 70%,
75: 75%,
80: 80%,
85: 85%,
90: 90%,
95: 95%,
100: 100%
);